import React, { Component } from 'react'


class IconContainer extends Component {
    constructor(props) {
        super(props)
    } 

    handleClick = (e) => {
        this.props.updateIcon(e.target.id, this.props.specieName)
    }
    
    
    render(){
        return (
            <div className='iconSelectorContainer' id='iconSelectorContainer'>
                {Object.keys(this.props.icons).map( (iconId, key) => { return <img key={key} id={iconId} src={this.props.icons[iconId]} alt={iconId} onClick={this.handleClick} /> })} 
            </div>
        )
    }
}


export default IconContainer;